#gradient-canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%;
  --gradient-color-1: #0f0f0f; 
  --gradient-color-2: #007add; 
  --gradient-color-3: #0f0f0f;  
  --gradient-color-4: #0f0f0f;
  z-index: -999;
  overflow: hidden;
  box-sizing: border-box;
}

@media screen and (max-width: 750px) {
  #gradient-canvas {
    height: 160%!important;
    margin: auto;
  }
}